import React from 'react';
import { AvatarWrap, AvatarHighlightWrapper } from './styled';
import { Avatar as BaseAvatar, IAvatarProps as IBaseAvatarProps } from '../../display/avatar/Avatar';
import { AVATAR_SIZE } from './constants';

export type IAvatarProps = {
   /**
    * 边框颜色
    */
   color?: string;
   /**
    * 头像高亮
    */
   highlight?: boolean;
} & Omit<IBaseAvatarProps, 'size' | 'shape'>;

export const Avatar = React.forwardRef((props: IAvatarProps, ref) => {
  const {
    color,
    highlight: isHighlight,
    ...rest
  } = props;
  const wrapStyle = {
    '--hover-color': color,
    border: !color ? 'none' : undefined,
  }
  const avata = (
    <AvatarWrap
      style={wrapStyle}
      ref={ref}
      className="wdn-avatar-group-item-wrapper"
    >
      <BaseAvatar
        shape='circle'
        size={AVATAR_SIZE}
        style={{
          position: 'absolute',
          top: -1,
          right: -1,
        }}
        {...rest}
      />
    </AvatarWrap>
  );
  return isHighlight ? (
    <AvatarHighlightWrapper>
      {avata}
    </AvatarHighlightWrapper>
  ) : avata;
});
